.cruises h2 {
  @include font(28px, 150%, 600);
  margin: 0 0 60px 0;
  text-align: center;

  @include vp-1023 {
    @media (max-width: ($vp-1024 - 1px)) {
      @include font(26px, 150%, 600);
    }
  }

  @include vp-767 {
    @media (max-width: ($vp-768 - 1px)) {
      @include font(24px, 150%, 600);
    }
  }
}

.cruises .container {
  padding: 95px 30px 120px 30px;

  @include vp-1023 {
    @media (max-width: ($vp-1024 - 1px)) {
      grid-template-columns: repeat(auto-fit, 309px);
      padding: 100px 60px;
    }
  }

  @include vp-767 {
    @media (max-width: ($vp-768 - 1px)) {
      padding: 65px 25px;
    }
  }
}

.cruises__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, 260px);
  gap: 30px 30px;

  @include vp-1023 {
    @media (max-width: ($vp-1024 - 1px)) {
      grid-template-columns: repeat(auto-fit, 309px);
      justify-content: center;
    }
  }

  @include vp-767 {
    @media (max-width: ($vp-768 - 1px)) {
      grid-template-columns: repeat(auto-fit, 270px);
    }
  }
}

.cruises__item {
  cursor: pointer;
  width: 260px;
  height: 386px;

  &:hover > .cruises__item-front {
    transform: perspective(600px) rotateY(-180deg);
  }

  &:hover > .cruises__item-back {
    transform: perspective(600px) rotateY(0deg);
  }

  @include vp-1023 {
    @media (max-width: ($vp-1024 - 1px)) {
      width: 309px;
    }
  }

  @include vp-767 {
    @media (max-width: ($vp-768 - 1px)) {
      width: 270px;
    }
  }
}

.cruises__item-front img {
  @include vp-1023 {
    @media (max-width: ($vp-1024 - 1px)) {
      width: 309px;
    }
  }

  @include vp-767 {
    @media (max-width: ($vp-768 - 1px)) {
      width: 270px;
    }
  }
}

.cruises__item-front h3 {
  @include font(18px, 150%, 500);
  margin: 0;
  position: absolute;
  bottom: 30px;
  left: 30px;
  color: $second-color;
}

.cruises__item-front,
.cruises__item-back {
  width: 260px;
  height: 386px;
  overflow: hidden;
  backface-visibility: hidden;
  position: absolute;
  transition: transform 0.6s linear;

  @include vp-1023 {
    @media (max-width: ($vp-1024 - 1px)) {
      width: 309px;
    }
  }

  @include vp-767 {
    @media (max-width: ($vp-768 - 1px)) {
      width: 270px;
    }
  }
}

.cruises__item-front {
  transform: perspective(600px) rotateY(0deg);
}

.cruises__item-back {
  transform: perspective(600px) rotateY(180deg);
  background-color: #011c40;
}

.cruises__back-content h3 {
  @include font(18px, 150%, 500);
  margin: 0 0 25px 0;
  color: $second-color;
}

.cruises__back-content {
  padding: 27px 25px;

  @mixin vp-1023 {
    @media (max-width: ($vp-1024 - 1px)) {
      width: 239px;
    }
  }
}

.cruises__info-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.cruises__info-item:not(:last-child) {
  margin-bottom: 20px;
}

.cruises__info-item h4 {
  @include font(14px, 140%, 500);
  margin: 0;
  color: $second-color;
}

.cruises__info-item p {
  @include font(14px, 140%, 400);
  margin-bottom: 20px;
  color: $supportive-color;
}

.cruises__item-button {
  width: 100%;
  display: block;
  padding: 13px;
}
